import img5 from './ico/app.svg'
import img6 from './ico/check.svg'
import img7 from './ico/html.svg'
import img8 from './ico/css.svg'
import img9 from './ico/js.svg'
import img10 from './ico/jquery.svg'
import img11 from './ico/link.svg'
import img12 from './ico/mango.svg'
import img13 from './ico/nodejs.svg'
import img14 from './ico/redux.svg'
import img15 from './ico/sass.svg'
